import "./navbar.css";
import logo from "../images/logo.png";
import login from "../images/login.png";
import download_btn from "../images/downloadbtn.png";
import toggle_btn from "../images/threebtn.png";
import { Link } from "react-router-dom";
import { useState } from "react";

function Navbar() {
  let [navpicker, setnavpicker] = useState(false);
  let [pickerwidth, setpickerwidth] = useState(0);
  let [pickerleft, setpickerleft] = useState(0);
  let [toggle, settoggle] = useState(false);

  return (
    <>
      <header>
        <nav className="navbar">
          <ul>
            <Link to="/">
              <img className="logo" src={logo} alt="logo"></img>
            </Link>
            <Link to="/">
              <li
                onMouseOver={(e) => {
                  setnavpicker(true);
                  setpickerleft(e.currentTarget.offsetLeft);
                  setpickerwidth(e.currentTarget.offsetWidth);
                }}
              >
                
              </li>
            </Link>
            <Link to="/news">
              <li
                onMouseOver={(e) => {
                  setnavpicker(true);
                  setpickerleft(e.currentTarget.offsetLeft);
                  setpickerwidth(e.currentTarget.offsetWidth);
                }}
              >
                
              </li>
            </Link>
            <Link to="/character">
              <li
                onMouseOver={(e) => {
                  setnavpicker(true);
                  setpickerleft(e.currentTarget.offsetLeft);
                  setpickerwidth(e.currentTarget.offsetWidth);
                }}
              >
                
              </li>
            </Link>
          </ul>
          <Link to="/download">
            <button className="download-btn">
              <img src={download_btn} alt="download button" />
            </button>
          </Link>
          <Link to="/login">
            <button className="login-btn">
              <span></span>
              <img src={login} alt="login button"></img>
            </button>
          </Link>
        </nav>
        {navpicker ? (
          <div
            style={{
              position: "absolute",
              zIndex: 1,
              transition: "all 0.2s ease-in-out 0s",
              backgroundColor: "rgb(105, 224, 255)",
              height: "5px",
              left: `${pickerleft}px`,
              width: `${pickerwidth}px`,
              top: "0px",
            }}
          />
        ) : null}
        <button
          className="toggle-btn"
          onClick={() => {
            settoggle(!toggle);
          }}
        >
          <img src={toggle_btn} alt="toggle button" />
        </button>
        {toggle ? (
          <ul className="toggle-menu">
            <Link to="/" className="Link-textdecoration">
              <li></li>
            </Link>
            <Link to="/news" className="Link-textdecoration">
              <li></li>
            </Link>
            <Link to="/character" className="Link-textdecoration">
              <li></li>
            </Link>
          </ul>
        ) : null}
      </header>
    </>
  );
}

export default Navbar;
