import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Navigation } from "swiper";

import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "./swiper.css";

import swiper_1 from "../images/swiper_1.jpg"
import swiper_2 from "../images/swiper_2.jpg"
import swiper_3 from "../images/swiper_3.jpg"

export default function MySwiper() {
  return (
    <>
      <Swiper
        slidesPerView={1}
        spaceBetween={30}
        loop={true}
        navigation={true}
        modules={[Pagination, Navigation]}
        speed={1000}
        className="mySwiper"
      >
        <SwiperSlide><img src={swiper_1} alt="firstimage"></img></SwiperSlide>
        <SwiperSlide><img src={swiper_2} alt="secondimage"></img></SwiperSlide>
        <SwiperSlide><img src={swiper_3} alt="thirdimage"></img></SwiperSlide>
      </Swiper>
    </>
  );
}
