import "./footer.css";
import logo from "../images/logo.png";
import one_five_grade from "../images/one-five-grade.png";
import violence from "../images/violence.png";
import sexual from "../images/sexual.png";

import { Link } from "react-router-dom";

function Footer() {
  return (
    <>
      <footer>
        <div className="footer">
          <img src={logo} alt="logo" />
          <div className="footer-text-links">
            <Link to="" className="text-decoration">
              
            </Link>
            <Link to="" className="text-decoration">
              
            </Link>
            <Link to="" className="text-decoration">
              
            </Link>
            <Link to="" className="text-decoration">
              
            </Link>
          </div>
          <div className="game-info-img">
            <img src={one_five_grade} alt="15 age grade" />
            <img src={violence} alt="violence" />
            <img src={sexual} alt="sexual" />
          </div>
          <div className="footer-texts">
            <div>
              "PlayStation Family Mark," "PS5 logo" and "PS4 logo" are
              registered trademarks or trademarks of Sony Interactive
              Entertainment Inc.
            </div>
            <div>
              Epic, Epic Games, Epic Games Store, the Epic Games Store logo, and
              Epic Online Services are trademarks and/or registered trademarks
              of Epic Games. All other trademarks are the property of their
              respective owners.
            </div>
            <div className="copyright">Copyright  MIHOYO. All Rights Reserved.</div>
          </div>
        </div>
      </footer>
    </>
  );
}

export default Footer;
