import "./character.css";
import { useEffect, useState } from "react";
import axios from "axios";

function Character() {

  const [data, setData] = useState([]);
  const [page, setPage] = useState(1);
  const limit = 5;
  const offset = (page - 1) * limit;

  useEffect(() => {
    axios
      // .get("./api")
      .get("dataCharacter.json") // CORS    
      .then((response) => {
        setData(response.data.rows);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  function Pagination({ total, limit, page, setPage }) {
    const numPages = Math.ceil(total / limit) + 1; //   , +1   
    const showPageNumber = 10; //     
    const showPageGroup = Math.ceil(page / showPageNumber); //     ex) 1 -> 1 ~ 10 / 2 -> 11 ~ 20 / 3 -> 21 ~ 30
    const correctionForFunMap =
      showPageGroup === 1 ? 0 : showPageGroup * 10 - 10; // map index 10, 20, 30  

    let lastPage = showPageGroup * 10 + 1;
    if (lastPage > numPages) lastPage = numPages;

    const firstPage = (showPageGroup - 1) * 10 <= 0 ? 1 : (showPageGroup - 1) * 10 + 1;
    return (
      <>
        <nav className="pagination">
          <button
            className="page-btn"
            onClick={() => setPage(page - 1)}
            disabled={page === 1}
          >
            &lt;
          </button>
          {Array(numPages)
            .fill()
            .slice(firstPage, lastPage)
            .map((_, i) => (
              <button
                key={i + 1 + correctionForFunMap}
                className={ i + 1 + correctionForFunMap === page ? "page-btn-select" : "page-btn" }
                onClick={() => setPage(i + 1 + correctionForFunMap)}
              >
                {i + 1 + correctionForFunMap}
              </button>
            ))}
          <button
            className="page-btn"
            onClick={() => setPage(page + 1)}
            disabled={page + 1 === numPages} // page + 1   
          >
            &gt;
          </button>
        </nav>
      </>
    );
  }

  return (
    <>
      <div className="character_container">
        <div className="character_main_img">
          <table className="character_table">
            <thead>
              <tr>
                <th colSpan={1} className="profile-picture"></th>
                <th colSpan={1} className="character-name"></th>
                <th colSpan={1} className="character-sub"></th>
              </tr>
            </thead>
            <tbody>
              {data
                .slice(offset, offset + limit)
                .map(({ characterId, characterName }, index) => {
                  return (
                    <tr key={index}>
                      <td className="profile-picture-row">
                        <img
                          src={`https://img-api.neople.co.kr/cy/characters/${characterId}`}
                          alt="character icon"
                        ></img>
                      </td>
                      <td className="character-name-row">{characterName}</td>
                      <td className="character-sub-row"> .</td>
                    </tr>
                  );
                })}
            </tbody>
          </table>
          <Pagination
            total={data.length}
            limit={limit}
            page={page}
            setPage={setPage}
          />
        </div>
      </div>
    </>
  );
}

export default Character;
