import { Link } from "react-router-dom";
import { useState, useEffect } from "react";

import "./main.css";
import Modal from "./modal";
import MySwiper from "../swiper/swiper";

function Main() {
  let [modal, setModal] = useState(false);

  const keyupevent = (e) => {
    if (modal === true && e.key === "Escape") {
      setModal(!modal);
    }
  };

  const mouseupevent = (e) => {
    if (modal === true) {
      setModal(!modal);
    }
  };

  useEffect(() => {
    document.addEventListener("keyup", keyupevent);
    document.addEventListener("mouseup", mouseupevent);
    return () => {
      document.removeEventListener("keyup", keyupevent);
      document.removeEventListener("mouseup", mouseupevent);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [modal]);

  return (
    <>
      <div className="container">
        <div className="main_image" />
        <div className="trailer">
          <div className="trailer-text"></div>
          <div
            className="trailer-modal"
            onClick={() => {
              setModal(!modal);
            }}
          ></div>
          {modal === true ? (
            <>
              <div className="trailer-mask" style={{ zIndex: 996 }}></div>
              <Modal />
            </>
          ) : null}
          <Link to="//www.youtube.com/watch?v=Zk4c5r15RJI" target="_blank">
            <div className="trailer-modal-mobile"></div>
          </Link>
        </div>

        <div className="crossflatforms">
          <Link
            to="//www.playstation.com/ko-kr/games/genshin-impact/"
            target="_blank"
          >
            <div className="crossflatforms-btn play-station" />
          </Link>
          <Link to="//apps.apple.com/app/id1517783697" target="_blank">
            <div className="crossflatforms-btn app-store" />
          </Link>
          <Link
            to="//play.google.com/store/apps/details?id=com.miHoYo.GenshinImpact"
            target="_blank"
          >
            <div className="crossflatforms-btn google-play" />
          </Link>
          <Link
            to="//www.microsoft.com/ko-kr/software-download/windows10%5C"
            target="_blank"
          >
            <div className="crossflatforms-btn windows" />
          </Link>
        </div>
        <div className="main_to_swiper">
          <p className="game_feature"></p>
        </div>
        <div className="my-swiper-wrapper">
          <MySwiper />
        </div>
      </div>
    </>
  );
}

export default Main;
